:root {
  --main-color: #1f1f1f;
  --border-radius: 5px;
  --border: 2px solid var(--main-color);
  --box-shadow: 3px 3px 0 0 var(--main-color);
  --active-color: #facc15;
}

html {
  color: #222;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 1rem;
  font-weight: 500;
}

body {
  background: #e6e6fa;
  width: 96%;
  max-width: 1024px;
  margin: 0 auto;
}

h1 {
  font-weight: 900;
}

input, select, label {
  cursor: pointer;
  line-height: 1.3;
}

input, select {
  border: var(--border);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  background: #fff;
  flex: auto;
  min-width: 0;
  max-width: 100%;
  padding: 5px 10px;
  font-size: 1rem;
  display: inline-flex;
}

label {
  flex: none;
  font-size: .9rem;
  font-weight: 600;
}

.content {
  gap: 20px;
  margin: 50px 0;
  display: flex;
}

.content > div {
  flex: auto;
}

.time-zone {
  max-width: 77%;
}

.example, .result {
  border: var(--border);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  background: #fff;
  margin: 0 0 10px;
  padding: 5px 10px;
  line-height: 1.3;
}

div.examples {
  flex: 0 0 24%;
}

.example {
  cursor: pointer;
  transition: background .1s ease-in-out;
}

.example:hover, .example.active {
  background: var(--active-color);
}

.example:last-of-type {
  margin-bottom: 0;
}

.options {
  flex-flow: column wrap;
  align-items: flex-start;
  gap: 10px;
  display: flex;
}

.optgroup {
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  width: 100%;
  display: flex;
}

.form-container {
  max-width: 28%;
}

.output {
  flex-direction: column;
  display: flex;
}

.code-container {
  background: var(--active-color);
  border: var(--border);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  flex: 1 0 auto;
  padding: 20px;
  position: relative;
}

.code {
  margin: 0;
  padding: 0;
  font-family: Courier New, Courier, monospace;
  font-size: .95rem;
  font-weight: 600;
}

.copy {
  border: var(--border);
  border-radius: var(--border-radius);
  min-width: 120px;
  box-shadow: var(--box-shadow);
  cursor: pointer;
  background: #fff;
  padding: 7px 16px;
  font-size: 1rem;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.links {
  text-align: center;
  margin: 20px 0 0;
}

.links a {
  color: var(--main-color);
}

.footer-text {
  text-align: center;
  margin: 20px 0 30px;
  font-size: .9em;
}

.footer-text a {
  color: var(--main-color);
}

@media screen and (width <= 700px) {
  .content {
    flex-wrap: wrap;
  }

  .content > div {
    flex: 100%;
    max-width: 100%;
  }

  .code-container {
    min-height: 200px;
  }
}
